import React, { Component } from 'react';
import './AllMaps.css';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu, } from 'react-mdl';

class AllMaps extends Component {
    constructor(props) {
        super(props);
        this.state = { activeTab: 0 };
    }

    toggleCategories() {
            if(this.state.activeTab === 0){
                return(
                    <div className="projects-grid">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', background: 'url(data:image/jpeg;base64,)'}}></CardTitle>
                                <CardText>Ascent</CardText>
                                <CardActions border>
                                    <Button colored>Something</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                )
            }
            else if(this.state.activeTab === 1){
                return(
                    <div className="projects-grid">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQuzXHT2wGG-Nizr1J7NEASYCiGZ9MhEjiV5w&usqp=CAU)'}}></CardTitle>
                                <CardText>Bind</CardText>
                                <CardActions border>
                                    <Button colored>Something</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                )
            }      
            else if(this.state.activeTab === 2){
                return(
                    <div className="projects-grid">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', background: 'url(data:image/jpeg;base64,)'}}></CardTitle>
                                <CardText>Haven</CardText>
                                <CardActions border>
                                    <Button colored>Something</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                )
            }
            else if(this.state.activeTab === 3){
                return(
                    <div className="projects-grid">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQNlgsObjexesjIOojQr06mwfnXMOMaIY-eRg&usqp=CAU)'}}></CardTitle>
                                <CardText>Icebox</CardText>
                                <CardActions border>
                                    <Button colored>Something</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                )
            }
            else if(this.state.activeTab === 4){
                return(
                    <div className="projects-grid">
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                                <CardTitle style={{ color: '#fff', height: '176px', background: 'url(data:image/jpeg;base64,)'}}></CardTitle>
                                <CardText>Split</CardText>
                                <CardActions border>
                                    <Button colored>Something</Button>
                                </CardActions>
                                <CardMenu style={{color: '#fff'}}>
                                </CardMenu>
                        </Card>
                    </div>
                )
            }
    }

    render() { 
        return (
        <div className="category-tabs">
            <Tabs activeTab={ this.state.activeTab } onChange={(tabID) => this.setState({ activeTab: tabID })} ripple>
                <Tab>Ascent</Tab>
                <Tab>Bind</Tab>
                <Tab>Haven</Tab>
                <Tab>Icebox</Tab>
                <Tab>Split</Tab>
            </Tabs>

            <section className='allmapbg'>
                    <Grid>
                        <Cell col={12}><div className="content">{this.toggleCategories()}</div></Cell>
                    </Grid>             
            </section>
        </div>
        );
    }
}

export default AllMaps;