import React from 'react';
import './App.css';
import Home from './Components/Home/Home';
import About from './Components/About/About';
import AllAgents from './Components/Agents/AllAgents';
import AllMaps from './Components/Maps/AllMaps';
import AllWeapons from './Components/Weapons/AllWeapons';
import AllMeta from './Components/Meta/AllMeta';
import {Layout, Header, HeaderRow, Navigation, Content, Icon, FABButton } from 'react-mdl';
import {BrowserRouter as Router, Switch, Route, Link} from 'react-router-dom';
import StickyFooter from './Components/StickyFooter/StickyFooter';

function App() {
  return (
      <div className="App">

        <Router>

          <div className="demo-big-content">
            <Layout>
                <Header waterfall>

                    <HeaderRow title="VALPRO 🎮">
                      <FABButton mini colored>
                        <Icon name="double_arrow" />
                      </FABButton>
                    </HeaderRow>

                    <HeaderRow>
                        <Navigation>
                            <Link to="/">HOME</Link>
                            <Link to="/About">ABOUT</Link>
                            <Link to="/AllAgents">AGENTS</Link>
                            <Link to="/AllMaps">MAPS</Link>
                            <Link to="/AllWeapons">WEAPONS</Link>
                            <Link to="/AllMeta">META</Link>
                        </Navigation>
                    </HeaderRow>
                </Header>
                <Content>
                    
                  <Switch>
                    <Route path='/About' component={About} />
                    <Route path='/AllAgents' component={AllAgents} />
                    <Route path='/AllMaps' component={AllMaps} />
                    <Route path='/AllWeapons' component={AllWeapons} />
                    <Route path='/AllMeta' component={AllMeta} />
                    <Route path='/' component={Home} />
                  </Switch>
                  <StickyFooter />

                </Content>
            </Layout>
          </div>
      
        </Router>
    
      </div>
  );
}

export default App;
