import React from 'react';
import {Footer,FooterSection,FooterLinkList} from 'react-mdl';

function footer(){
  return(
    <Footer size="mini" className='footer'>
    <FooterSection>
        <FooterLinkList>
          © Valpro 2020.
        </FooterLinkList>
    </FooterSection>
</Footer>

  );
}

export default footer;